@import "fonts";
%marginTop {
  margin-top: 20px; }
* {
  padding: 0; }
body {
  font: {
    family: "Montserrat" , "Arial" , sans-serif;
    size: 15px; } }
header {
  position: fixed;
  background-color: #a51b1b;
  left: 0;
  right: 0;
  z-index: 3; }
h1 {
  font-size: 25px;
  text-align: center;
  @extend %marginTop; }
nav {
  @extend %marginTop; }
img[src="img/logo.png"] {
  width: 100%; }

/*Progress Bar*/
.progress,h2 {
  margin-top: 75px; }

/*Carousel*/
.carousel {
  margin-top: 50px; }
.carousel-indicators {
  .active {
    background-color: red; } }
img[src="img/first.jpg"] + div,img[src="img/second.jpg"] + div,img[src="img/third.jpg"]  + div {
  background: #000000;
  position: relative;
  bottom: 60px;
  height: 30px;
  text-align: center;
  p {
    color: #fff3cd; } }
.carousel-indicators {
  bottom: 20px; }
.carousel-control-prev,.carousel-control-next {
  height: 93%; }
